//
//	LOGO SVG ANIMATION logo-3
// ------------------------------------------------------

.logo-3-c {
  fill: $color-text;
  &1 { animation-name: logo_3_c_1; }
  &2 { animation-name: logo_3_c_2; }
  &3 { animation-name: logo_3_c_3; }
  &4 { animation-name: logo_3_c_4; }
  &5 { animation-name: logo_3_c_5; }
  &6 { animation-name: logo_3_c_6; }
  &7 { animation-name: logo_3_c_7; }
  &8 { animation-name: logo_3_c_8; }
}

@keyframes logo_3_c_1{
  20%{ opacity: 0; }
  30%{ opacity: 1; }
  0%{ opacity: 0; }
  100%{ opacity: 1; }
}

@keyframes logo_3_c_2{
  30%{ opacity: 0; }
  40%{ opacity: 1; }
  0%{ opacity: 0; }
  100%{ opacity: 1; }
}

@keyframes logo_3_c_3{
  70%{ opacity: 0; }
  80%{ opacity: 1; }
  0%{ opacity: 0; }
  100%{ opacity: 1; }
}

@keyframes logo_3_c_4{
  50%{ opacity: 0; }
  60%{ opacity: 1; }
  0%{ opacity: 0; }
  100%{ opacity: 1; }
}

@keyframes logo_3_c_5{
  60%{ opacity: 0; }
  70%{ opacity: 1; }
  0%{ opacity: 0; }
  100%{ opacity: 1; }
}

@keyframes logo_3_c_6{
  40%{ opacity: 0; }
  50%{ opacity: 1; }
  0%{ opacity: 0; }
  100%{ opacity: 1; }
}

@keyframes logo_3_c_7{
  0%{ opacity: 0; }
  10%{ opacity: 1; }
  100%{ opacity: 1; }
}

@keyframes logo_3_c_8{
  10%{ opacity: 0; }
  20%{ opacity: 1; }
  0%{ opacity: 0; }
  100%{ opacity: 1; }
}

