html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

html,
body {
  color: #F22F41;
  font-family: "Montserrat", sans-serif;
  background: #191932;
  height: 100%; }

a {
  text-decoration: none;
  color: inherit; }

.container {
  max-width: 1170px;
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: 70px auto;
  grid-template-areas: "t"  "c";
  height: 100vh;
  padding: 1em;
  margin: 0 auto;
  overflow: hidden;
  position: relative; }

.btn {
  font-weight: 900; }
  .btn--link {
    position: relative;
    font-size: .9em; }
    .btn--link:before {
      content: '';
      position: absolute;
      bottom: -10px;
      height: 3px;
      width: 100%;
      background: #F22F41; }

.top_bar {
  grid-area: t;
  display: flex;
  flex-direction: row;
  justify-content: center; }

.logo_ico_wrapper {
  position: relative; }

.logo_ico {
  display: none; }

.logo--active {
  display: block; }

.logo--deactive {
  display: none; }

.logo_name {
  position: relative;
  margin: 0 0 0 10px;
  align-self: center;
  text-transform: uppercase;
  font-size: 2em;
  font-weight: 900; }
  .logo_name:after {
    content: '2';
    font-size: .5em;
    position: absolute;
    top: 0;
    animation: fadeIn 2s; }

.content {
  grid-area: c;
  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-template-rows: auto;
  grid-template-areas: "m a"; }

@media screen and (max-width: 600px) {
  .content {
    grid-template-columns: 1fr;
    grid-template-rows: 3fr 1fr;
    grid-template-areas: "a" "s"; } }

.nav_wrapper {
  grid-area: m;
  align-self: center;
  justify-self: start; }

@media screen and (max-width: 600px) {
  .nav_wrapper {
    display: none; } }

.nav {
  counter-reset: li;
  list-style: none;
  *list-style: decimal-leading-zero;
  font-size: 0.8em;
  text-transform: uppercase;
  font-weight: 700;
  padding-left: 40px; }
  .nav_item {
    position: relative;
    margin-bottom: 2.5em;
    height: 3px;
    width: 25px;
    background: #2a2a54;
    transition: .5s; }

.nav_item:before {
  content: "0" counter(li) ".";
  counter-increment: li;
  position: absolute;
  left: -3em;
  top: 50%;
  margin-top: -1em;
  height: 2em;
  width: 2em;
  line-height: 2em;
  text-align: center;
  color: #2a2a54; }

.nav_item:nth-child(1) {
  animation: nav 0.5s; }

.nav_item:nth-child(2) {
  animation: nav 1s; }

.nav_item:nth-child(3) {
  animation: nav 1.5s; }

.nav_item:nth-child(4) {
  animation: nav 2s; }

.nav_item:nth-child(5) {
  animation: nav 2.5s; }

.nav_item--active {
  background: #F22F41;
  width: 40px; }
  .nav_item--active:before {
    color: #F22F41; }

.nav_item--deactive {
  background: #2a2a54;
  width: 25px; }
  .nav_item--deactive:before {
    color: #2a2a54; }

.page {
  position: relative;
  grid-area: a;
  align-self: center; }

.page_content {
  opacity: 0;
  transform: translate3d(50%, 0, 0);
  transition: transform 1s, opacity 1s; }

.page_title {
  margin-top: 0;
  font-size: 2.5em; }

.page_article {
  font-size: .9em;
  line-height: 30px;
  color: #8787c3; }

.page.active .page_content {
  opacity: 1;
  transform: translate3d(0, 0, 0); }

.page.inactive .page_content {
  opacity: 0;
  transform: scale(0.95); }

@media screen and (max-width: 600px) {
  .page_content {
    text-align: center; }
  .page_title {
    font-size: 2em; } }

.intro_wrapper {
  position: relative; }

.intro {
  animation: slideInRight 1s; }
